:host {
  --pfe-modal--MaxWidth: 70vw;
  --pfe-modal--MinWidth: 50vw;
  --pfe-modal--MaxWidth--mobile: 94vw;
  --pfe-modal--MaxHeight: 90vh;
  --pfe-modal--Padding: calc(var(--pfe-theme--container-padding, 16px) * 2) calc(var(--pfe-theme--container-padding, 16px) * 3.5) calc(var(--pfe-theme--container-padding, 16px) * 2) calc(var(--pfe-theme--container-padding, 16px) * 2);
  --pfe-modal__overlay--BackgroundColor: var(--pfe-theme--color--overlay, rgba(45, 45, 45, 0.5));
  --pfe-modal__close--size: calc(var(--pfe-theme--ui--element--size, 20px) - 4px);
  display: block;
  position: relative;
}

@media screen and (max-height: 640px) {
  :host {
    --pfe-modal--Padding: 0 0 0 calc(var(--pfe-theme--container-padding, 16px) * 2);
  }
}

.pfe-modal__outer {
  display: flex;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: var(--pfe-theme--zindex--modal, 99);
}

.pfe-modal__outer[hidden] {
  display: none;
}

.pfe-modal__overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: var(--pfe-modal__overlay--BackgroundColor);
  cursor: pointer;
}

.pfe-modal__overlay[hidden] {
  display: none;
}

.pfe-modal__window {
  position: relative;
  max-width: var(--pfe-modal--MaxWidth--mobile);
  min-width: var(--pfe-modal--MinWidth);
  max-height: var(--pfe-modal--MaxHeight);
  margin: 0 auto;
  box-shadow: var(--pfe-theme--box-shadow--lg, 0 0.1875rem 0.4375rem 0.1875rem rgba(19, 19, 19, 0.13), 0 0.6875rem 1.5rem 1rem rgba(19, 19, 19, 0.12));
  background-color: var(--pfe-theme--color--surface--lightest, #fff);
  border-radius: var(--pfe-theme--ui--border-radius, 2px);
}

@media screen and (min-width: 640px) {
  .pfe-modal__window {
    max-width: var(--pfe-modal--MaxWidth);
  }
}

.pfe-modal__container {
  position: relative;
  max-height: inherit;
}

.pfe-modal__container[hidden] {
  display: none;
}

.pfe-modal__content {
  overflow-y: auto;
  overscroll-behavior: contain;
  max-height: var(--pfe-modal--MaxHeight);
  padding: var(--pfe-modal--Padding);
  box-sizing: border-box;
}

@media screen and (max-height: 640px) {
  .pfe-modal__content {
    --pfe-modal--Padding: var(--pfe-theme--container-padding, 16px) calc(var(--pfe-theme--container-padding, 16px) * 3) var(--pfe-theme--container-padding, 16px) var(--pfe-theme--container-padding, 16px);
  }
}

:host(:not([has_header])) .pfe-modal__content ::slotted(*:nth-child(2)),
:host(:not([has_header])) .pfe-modal__content ::slotted(*:nth-child(1)) {
  margin-top: 0 !important;
}

.pfe-modal__content ::slotted([slot="pfe-modal--header"]) {
  margin-top: 0 !important;
}

.pfe-modal__close {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: calc(var(--pfe-theme--container-padding, 16px) * .25);
  right: calc(var(--pfe-theme--container-padding, 16px) * .25);
  cursor: pointer;
  line-height: .5;
  padding: var(--pfe-theme--container-padding, 16px);
}

@media screen and (min-width: 640px) {
  .pfe-modal__close {
    top: var(--pfe-theme--container-padding, 16px);
    right: var(--pfe-theme--container-padding, 16px);
  }
}

@media screen and (max-height: 640px) and (min-width: 640px) {
  .pfe-modal__close {
    top: calc(var(--pfe-theme--container-padding, 16px) / 2);
    right: calc(var(--pfe-theme--container-padding, 16px) / 2);
  }
}

.pfe-modal__close > svg {
  fill: var(--pfe-broadcasted--text);
  height: var(--pfe-theme--ui--element--size, 20px);
  width: var(--pfe-theme--ui--element--size, 20px);
  height: var(--pfe-modal__close--size);
  width: var(--pfe-modal__close--size);
}
